<template>
    <div class="detail">
        <van-icon name="arrow-left" @click="back"/>
        <van-icon name="ellipsis" is-link @click="showPopup"/>
        <van-popup v-model="show" round position="top" closeable :style="{ height: '30%' }" >
            <div class="nav">
                <p class="title">快速导航</p>
                <ul>
                    <li @click="goPage(item.routeName)" v-for="(item,index) in navData" :key="index">
                        <van-icon :name="item.icon" color="rgb(255,156,0)"/>
                        <p>{{item.title}}</p>
                    </li>
                </ul>
            </div>
        </van-popup>
        <van-swipe @change="onChange">
          <van-swipe-item v-for="(item,index) in bannerData" :key="index"><img :src="item.pics_big" /></van-swipe-item>
          <template #indicator>
            <div class="custom-indicator">
              {{ current + 1 }}/{{bannerData.length}}
            </div>
          </template>
        </van-swipe>
        <div class="message">
            <div class="price">￥{{allData.goods_price}}</div>
            <div class="name"><h2>{{allData.goods_name}}</h2></div>
        </div>
        <div class="detailMessage" is-link @click="showedPopup">
            <span>参数</span>
            <span>品牌 材质...</span>
        </div>
        <van-popup class="detailList" v-model="showed" position="bottom" round :style="{ height: '80%' }" >
            <ul>
                <h3>基本信息</h3>
                <li v-for="(item,index) in detailData" :key="index">
                    <!-- <div> -->
                        <span>{{item.attr_name}}</span>
                        <span>{{item.attr_vals}}---{{item.attr_value}}</span>
                    <!-- </div> -->
                </li>
                <van-button block color='rgb(255,102,0)' @click="closePopup">确定</van-button>
            </ul>
        </van-popup>
        <van-divider
          :style="{ color: '#333', borderColor: '#333', padding: '0 100px' }"
        >
            <van-icon name="photo-o" />文字
        </van-divider>
        <div v-html="allData.goods_introduce"></div>
        <van-divider
          :style="{ color: '#333', borderColor: '#333', padding: '0 100px' }"
        >
            <van-icon name="bag-o" />推荐
        </van-divider>
        <div class="see clearfix">
            <p>看了又看</p>
            <div class="seeList fl" v-for="(item,index) in moreData" :key="index" @click="goDetail(item.goods_id)">
                <img v-lazy="item.goods_big_logo" />
                <ul>
                    <li>{{item.goods_name}}</li>
                    <li>￥{{item.goods_price}}</li>
                </ul>
            </div>
        </div>
        <!-- <liked></liked> -->
        <van-goods-action>
          <van-goods-action-icon :icon="isLike ? 'star' : 'star-o'" text="收藏" color="yellow" @click="liked"/>
          <van-goods-action-icon icon="cart-o" text="购物车" :badge="num == 0 ? '' : num" @click="goShopcart"/>
          <van-goods-action-button type="danger" text="立即购买" />
          <van-goods-action-button type="warning" text="加入购物车" @click="add(goods)"/>
        </van-goods-action>
    </div>
</template>

<script>
    import {createNamespacedHelpers} from "vuex";
    const {mapState,mapMutations} = createNamespacedHelpers("cart");
    // import liked from "../components/Liked";
    import "../assets/css/detail.less";
    export default {
        // components:{
        //     liked
        // },
        data(){
            return{
                allData:[],
                detailData:[],
                bannerData:[],
                moreData:[],
                current: 0,
                show: false,
                showed: false,
                active: 0,
                caid:20,
                navData:[
                    {
                        routeName: 'Home',
                        title:'首页',
                        icon:'wap-home-o'
                    },
                    {
                        routeName: 'GoSearch',
                        title:'搜索',
                        icon:'search'
                    },
                    {
                        routeName: 'Classification',
                        title:'分类',
                        icon:'apps-o'
                    },
                    {
                        routeName: 'Shopcart',
                        title:'购物车',
                        icon:'shopping-cart-o'
                    },
                    {
                        routeName: 'My',
                        title:'我',
                        icon:'user-o'
                    }
                ],
                isLike:false,
                goods:{}
            }
        },
        created(){
            this.goods_id = this.$route.query.goods_id
            this.Detail(this.goods_id);
            this.cid = Math.ceil(Math.random()*1481+1)
            this.List(this.cid);
        },
        methods:{
            ...mapMutations(["add"]),
            liked(){
                this.isLike = !this.isLike;
                //开启加载提示
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
                    duration: 0
                });
                if(this.isLike){
                    this.$toast("已收藏");
                }else{
                    this.$toast("已取消收藏");
                }
            },
            goShopcart() {
                this.$router.push({name: 'Shopcart'});
            },
            goPage(name) {
                this.$router.push({name});
            },
            showPopup() {
                this.show = true;
            },
            showedPopup() {
                this.showed = true;
            },
            onChange(index) {
                this.current = index;
            },
            closePopup(){
                this.showed = false;
            },
            Detail(goods_id) {
                //开启加载提示
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: 'GET',
                    url: '/goods/detail',
                    params: {
                        goods_id,
                    }
                }).then(result => {
                    this.$toast.clear();
                    if (result.data.meta.status == 200) {
                        
                        this.allData = result.data.message;
                        this.detailData = result.data.message.attrs;
                        this.bannerData = result.data.message.pics;
                        this.goods.id = result.data.message.goods_id;
                        this.goods.name = result.data.message.goods_name;
                        this.goods.price = result.data.message.goods_price;
                        this.goods.num = 1;
                        this.goods.image = result.data.message.goods_small_logo;
                    }
                }).catch(err => {
                    this.$toast.clear();       
                })
            },
            List(cid) {
                //开启加载提示
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });

                this.axios({
                    method: 'GET',
                    url: '/goods/search',
                    params: {
                        cid,
                    }
                }).then(result => {
                    this.$toast.clear();
                    if (result.data.meta.status == 200) {
                        
                        this.moreData = result.data.message.goods;    
                    }
                }).catch(err => {
                    this.$toast.clear();       
                })
            },
            back() {
                this.$router.go(-1);
                // let hash = location.hash.slice(18);
                // 
                // if(hash == this.allData.goods_id){
                //     this.$router.go(-1);
                // }else{
                //     this.$router.go(0);
                // }
            },
            goDetail(goods_id) {
                this.$router.push({name: 'Detail', query: {goods_id}});
                this.$router.go(this.Detail);
            }
        },
        computed:{
            ...mapState({
                num:state=>state.num
            })
        },
    }
</script>

<style lang="less" scoped>
</style>